import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { LoopBackConfig, LoopBackAuth, App_usersApi, VideosApi } from '../../../../shared/loopback-sdk';
import * as moment from 'moment'; //for date manupalation
import {PaginatorModule} from 'primeng/primeng';
import { Lightbox, IAlbum, LightboxConfig } from 'angular2-lightbox';
import { CookieService } from 'angular2-cookie/core';
declare var hostname:any;
declare var sdkPrefix;
@Component({
  templateUrl: 'videos.component.html'
})
export class VideosComponent {
	createdByErrors: boolean=false;
	busy:any;
	videoData:any;
	display:boolean = false;
	userList:any;
	ifEmpty:boolean = false;
	userDetails:any;
	rolename:any;
	loading:boolean = true;
	_albums:any = [];
	exportData:any = [];
	filteredUserFullName: any;
	allUsersFullName: any;
	videoStatus:any="";
	videoTitle:any;
	createdAt:any;
	maxDate:Date;

	//variables for filter & pagination
	createdAtSearch:any;
	videoTitleSearch:any;
	videoStatusSearch:any="";
	paginationVal:any=true;
	perPageLimit:number = 10;
	offset:number=0;
	total:number;

	constructor(private uApi:App_usersApi, private videoApi:VideosApi,  private _cookieService: CookieService, private router: Router, private loopAuth:LoopBackAuth,private _lightbox: Lightbox) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles[0].name;
		this.maxDate = new Date();
	}

	ngOnInit(): void {
		this.videoStatus = 1;
		this.videoStatusSearch= 1;
		this.getCount();
	}

	getCount(){
		this.loading = true;
		if(this.createdAt){
			this.createdAt = moment(this.createdAt).format("YYYY-MM-DD hh:mm:ss.SSS");
		}

		var dataArrObj = {
			"title":this.videoTitleSearch,
			"created_at":this.createdAtSearch,
			"status":this.videoStatusSearch,
			"page":this.offset,
			"limit":this.perPageLimit
		}

		this.busy = this.videoApi.getVideosCount(dataArrObj).subscribe(
			data=>{
				this.total = data['result'][0]['total'];
				if(this.total <= 10){
					this.paginationVal = false;
				}else{
					this.paginationVal = true;
				}
				this.getData(dataArrObj);
			},
			err=>{
				this.loading = false;
			},
			()=>{

			}
		)
	}

	showDialog() {
		this.display = true;
	}

	paginate(event) {
        this.videoData = [];
		this.offset=event.page;
		this.paginationVal=true;
		if(this.createdAt){
			this.createdAt = moment(this.createdAt).format("YYYY-MM-DD hh:mm:ss.SSS");
		}
		var dataArrObj = {
			"name":this.videoTitleSearch,
			"created_at":this.createdAtSearch,
			"status":this.videoStatusSearch,
			"page":this.offset,
			"limit":this.perPageLimit
		}

		this.getData(dataArrObj);
	}

	searchData(){
		this.offset = 0;
		this.paginationVal=false;
		this.ifEmpty = false;
		this.videoData =[];
		this.videoTitleSearch = this.videoTitle;
		this.createdAtSearch = this.createdAt;
		this.videoStatusSearch = this.videoStatus;
		this.getCount();
	}

	resetPage(){
		this.offset = 0;
		this.paginationVal=false;
		this.createdByErrors = false;
		this.videoStatus = "";
		this.videoTitle = "";
		this.createdAt = "";
		this.createdAtSearch = "";
		this.videoStatusSearch = "";
		this.videoTitleSearch = "";
		this.getCount();
	}

	addVideo(){
		this.router.navigate(["videos/add-video"]);
	}

	getData(dataArrObj){
		this.loading = true;
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this._albums = [];
		this.busy=this.videoApi.getVideos(dataArrObj).subscribe(
			data=>{
				this.loading = false;
				var totalData = data.result.length;
				this.ifEmpty = false;
				if(totalData == 0){
					this.ifEmpty = true;
				}
				for(var i=0; i<totalData; i++){
					if(data.result[i].status == 1){
						data.result[i].status = 'Active';
					}else{
						data.result[i].status = 'Inactive';
					}
					if(data.result[i].logo_image){
						data.result[i].logo_image = data.result[i].logo_image+'?access_token='+loginFlagToken;
					}
					data.result[i].created_at = moment(data.result[i].createdAt).format("DD - MMM - YYYY");
					this._albums.push(
						{
							src:data.result[i].logo_image,
							thumb: data.result[i].logo_image
						}
					);
				}
				this.videoData = data.result;
			},
			err=>{
				this.loading = false;
			},
			()=>{}
		)
	}

	open(index: number): void {
		// open lightbox
		this._lightbox.open(this._albums, index, { positionFromTop: 60 });
	}
}


